import React, { Component } from 'react';

import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import '../split.css';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    image: {
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
    paper: {
        textAlign: 'justify',
        padding: theme.spacing.unit * 2,

    },
});



function getArrayStr(data) {
    var str = "";
    for (var j = 0; j < data.length; j++) {
        if (j === data.length - 1) {
            str = str + data[j].toString();
        } else {
            str = str + data[j].toString() + ", ";
        }
    }
    return (
        { str: str }
    );
}
function getElkeszitesStr(data) {
    return (
        data.map((item, index) => {
            return <ul key={index}>
                <p>{index + 1}. lépés:</p>
                <p>{item.text}</p>
            </ul>
        }
        )
    );
}
function getHozzavalokStr(data) {
    return (
        data.map((item, index) => {
            return (
                <ul key={index}>
                    {item.name.toString()}
                    {item.alapanyagok.map((alapanyag, index) => {
                        return (
                            <ul key={index}>
                                {alapanyag.mennyiseg}
                                {" "}
                                {alapanyag.mertekegyseg}
                                {" "}
                                {alapanyag.name}
                                {alapanyag.kaloria !== "" ? " (" + alapanyag.kaloria + " kcal)" : ""}
                            </ul>
                        );
                    })
                    }

                </ul>
            )
        }
        )
    );
}

class RecipePage extends Component {
    state = {
        dimensions: {
            width: 0,
            height: 0,
        },
        dimensionsOrig: {
            width: 0,
            height: 0,
        }

    };

    componentDidMount() {
        this.setState({
            dimensions: {
                width: this.containerAll.offsetWidth,
                height: this.container.offsetHeight,
            },
        });
        var im = new Image();
        im.onload = () => {
            this.setState({
                dimensionsOrig: {
                    width: im.width,
                    height: im.height,
                },

            });
        };
        im.src = this.props.state.fileUrl;
    }
    componentDidUpdate(prevProps, prevState) {
        if (this.state.dimensions.height !== this.refs.myImgContainer.clientHeight) {
            this.setState({
                dimensions: {
                    width: this.state.dimensions.width,
                    height: this.refs.myImgContainer.clientHeight,
                },
            });
        }

        var im = new Image();
        im.onload = () => {
            if (this.state.dimensionsOrig.height !== im.height) {

                this.setState({
                    dimensionsOrig: {
                        width: im.width,
                        height: im.height,
                    },

                });
            }
        };
        im.src = this.props.state.fileUrl;
    }

    getImageW() {
        try {
            const origH = this.state.dimensionsOrig.height;
            const origW = this.state.dimensionsOrig.width;
            const w = this.state.dimensions.width / this.props.part;
            const h = this.state.dimensions.height;
            const r = origH / origW;
            if (origH < origW) {
                return { w };
            } else {
                return parseInt(h / r);
            }

        } catch (error) {
            return this.state.dimensions.width;
        }

    }
    getImageH() {
        try {
            const origH = this.state.dimensionsOrig.height;
            const origW = this.state.dimensionsOrig.width / this.props.part;
            const h = this.state.dimensions.height;

            const w = this.state.dimensions.width;
            const r = origH / origW;
            if (origH < origW) {
                return parseFloat(w / r);
            } else {
                return h;
            }
        } catch (error) {
            return this.state.dimensions.height;
        }

    }
    handleImageLoaded({ target: img }) {
        this.setState({
            dimensionsOrig: {
                height: img.offsetHeight,
                width: img.offsetWidth
            }
        });
    }

    getImage() {
        if (this.props.state.fileUrl !== "") {
            return (
                <Grid item xs={6}>
                    <img id='sky' className='img' src={this.props.state.fileUrl} width={this.getImageW()} height={this.getImageH()} alt=""></img>
                </Grid>);
        } else {
            return;
        }
    }

    renderContent() {
        return (
            <div>
                <h5>Évszakok: {getArrayStr(this.props.state.evszak).str}</h5>
                <h5>Ünnepek: {getArrayStr(this.props.state.unnep).str}</h5>
                <h5>Diéta: {getArrayStr(this.props.state.dieta).str}</h5>
                <h5>Menü: {getArrayStr(this.props.state.menu).str}</h5>
                <h5>Napszak: {getArrayStr(this.props.state.napszak).str}</h5>
                <h5>Technika: {getArrayStr(this.props.state.technika).str}</h5>
                <h5>Elkészítési idő: {this.props.state.elkeszitesiIdo !== "" ? this.props.state.elkeszitesiIdo + " perc" : ""} {this.props.state.elokeszitesiIdo !== "" ? " (" + this.props.state.elokeszitesiIdo + " perc elők.)" : ""}</h5>
                <h5>Kalória: {this.props.state.kaloria} {this.props.state.kaloria !== "" ? " kcal" : ""}</h5>

            </div>
        );
    }

    getDetailsSize() {
        if (this.state.fileUrl === "" || this.state.fileUrl === undefined) {
            return 11;
        } else {
            return 5;
        }
    }

    render() {
        const { classes } = this.props;
        const { dimensions } = this.state;

        return (
            <div>
                <div style={{
                    padding: "20px",
                }} align="center">
                    <h2>{this.props.state.receptCim}</h2></div>
                <div ref={el => (this.containerAll = el)}>
                    <Grid container spacing={24} id="parent">
                        <Grid item xs={this.getDetailsSize()} style={{ marginTop: 20, marginLeft: 20 }}>


                            <div ref="myImgContainer">
                                <div className="Hello" ref={el => (this.container = el)}>
                                    {dimensions && this.renderContent()}
                                </div></div>
                        </Grid>


                        {this.getImage()}

                    </Grid>
                </div>
                <Grid item xs style={{ marginTop: 20, marginLeft: 20 }}>
                    <h5>Hozzávalók {this.props.state.numberOfPeople} főre: {getHozzavalokStr(this.props.state.hozzavalok)}</h5>
                    <h5 className={classes.paper} >Elkészítés: {getElkeszitesStr(this.props.state.elkeszites.step)}</h5>
                </Grid>
            </div>

        );
    }
}
RecipePage.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(RecipePage);