import React, { Component } from 'react';




import axios from 'axios';
import RecipeCard from "./RecipeCard"

import RecipePage from "./RecipePage"

import { getDefaultAlapanyag } from "./Hozzavalok.js"

import * as Constans from '../Constans';


import 'filepond/dist/filepond.min.css';
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css';


import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import '../split.css';

import Button from '@material-ui/core/Button';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import SaveIcon from '@material-ui/icons/Save';
import CloseIcon from '@material-ui/icons/Close';

import IconButton from '@material-ui/core/IconButton';

const styles = theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden',
        backgroundColor: theme.palette.background.paper,
    },

    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
    button: {
        margin: theme.spacing.unit,
    },
    leftIcon: {
        marginRight: theme.spacing.unit,
    },
    rightIcon: {
        marginLeft: theme.spacing.unit,
    },
    iconSmall: {
        fontSize: 20,
    },
});

class Search extends Component {
    constructor() {
        super();
        this.state = {
            recipeIndToShow: [],
            open: [false, false],
            recipes: [],
            receptCim: "",
            elkeszitesiIdo: "",
            kaloria: "",
            evszak: [],
            unnep: [],
            dieta: [],
            menu: [],
            napszak: [],
            technika: [],
            hozzavalokCounter: 2,
            hozzavalok: [
                {
                    id: 1,
                    name: "",
                    counter: 1,
                    alapanyagok: [
                        getDefaultAlapanyag(0),
                    ]
                }
            ],
            elkeszites: {
                counter: 2,
                step: [
                    {
                        id: 1,
                        text: "",
                    }
                ]
            }
        };
    }

    componentDidMount() {
        this.getAllRecipe(this.props.token, this.props.user);
    }

    onSubmit = (e) => {
        e.preventDefault();


    };
    onReceptCimChange(e) {
        this.setState({ receptCim: e.target.value });
    }
    onElkeszitesiIdoChange(e) {
        this.setState({ elkeszitesiIdo: e.target.value });
    }
    onKaloriaChange(e) {
        this.setState({ kaloria: e.target.value });
    }
    onEvszakChange(label, e) {
        const evszak = this.state.evszak
        let index
        if (e.target.checked) {
            evszak.push(label)
        } else {
            index = evszak.indexOf(label)
            evszak.splice(index, 1)
        }
        evszak.sort();
        this.setState({ evszak: evszak })
    }
    onUnnepChange(label, e) {
        const unnep = this.state.unnep
        let index
        if (e.target.checked) {
            unnep.push(label)
        } else {
            index = unnep.indexOf(label)
            unnep.splice(index, 1)
        }
        unnep.sort();
        this.setState({ unnep: unnep })
    }
    onDietaChange(label, e) {
        const dieta = this.state.dieta
        let index
        if (e.target.checked) {
            dieta.push(label)
        } else {
            index = dieta.indexOf(label)
            dieta.splice(index, 1)
        }
        dieta.sort();
        this.setState({ dieta: dieta })
    }
    onMenuChange(label, e) {
        const menu = this.state.menu
        let index
        if (e.target.checked) {
            menu.push(label)
        } else {
            index = menu.indexOf(label)
            menu.splice(index, 1)
        }
        menu.sort();
        this.setState({ menu: menu })
    }
    onNapszakChange(label, e) {
        const napszak = this.state.napszak
        let index
        if (e.target.checked) {
            napszak.push(label)
        } else {
            index = napszak.indexOf(label)
            napszak.splice(index, 1)
        }
        napszak.sort();
        this.setState({ napszak: napszak })
    }
    onTechnikaChange(label, e) {
        const technika = this.state.technika
        let index
        if (e.target.checked) {
            technika.push(label)
        } else {
            index = technika.indexOf(label)
            technika.splice(index, 1)
        }
        technika.sort();
        this.setState({ technika: technika })
    }
    getAllRecipe(token, user) {
        axios.get(Constans.APIUrl + 'recipe/', {
            params: {
                token: token,
                user: user,
            }
        })
            .then(
                res => {
                    this.setState({ recipes: res.data });
                }

            )
            .catch((error) => {
            });
    };
    onRecipeClick(index) {
        var inds = this.state.recipeIndToShow;
        if (!inds.includes(index)) {
            inds.push(index);
            this.setState({
                recipeShow: true,
                recipeIndToShow: inds
            });
        }
        var myImage = new Image(100, 200);
        myImage.src = 'picture.jpg';
        

    }

    render() {
        const { classes } = this.props;
        const val = 'auto'
        return (

            <div>
                <ExpansionPanel expanded={this.state.open[0]} onChange={(e, o) => this.setState({ open: [!this.state.open[0], this.state.open[1]] })}>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Typography className={classes.button}>Keresés</Typography>


                        <IconButton className={classes.button} aria-label="Close">
                            <CloseIcon />
                        </IconButton>
                        <IconButton className={classes.button} aria-label="Save">
                            <SaveIcon />
                        </IconButton>

                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <form onSubmit={this.onSubmit.bind(this)}>

                            <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>

                                <Button variant="contained" color="secondary" type="submit">
                                    Keresés
                                </Button>
                            </Grid>
                        </form>
                    </ExpansionPanelDetails>
                </ExpansionPanel>
                <ExpansionPanel expanded={this.state.open[1]} onChange={(e, o) => this.setState({ open: [this.state.open[0], !this.state.open[1]] })}>
                    <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                        <Typography className={classes.heading}>Keresési eredmények</Typography>
                    </ExpansionPanelSummary>
                    <ExpansionPanelDetails>
                        <div className={classes.root}>

                            <Grid container spacing={24}>
                                {
                                    this.state.recipes.map((recipe, index) => {
                                        let str = "";
                                        recipe.elkeszites.step.map((step) => {
                                            str = str + step.text + " ";
                                            return str;
                                        });
                                        
                                        return (
                                            <Grid item xs={val} key={recipe._id} onClick={() => this.onRecipeClick(index)}>
                                                <RecipeCard imagePath={recipe.fileUrl}
                                                    recipeTitle={recipe.receptCim}
                                                    details={str.substr(0, Math.min(200, str.length))} />
                                            </Grid>
                                        );
                                    })
                                }
                            </Grid>
                        </div>
                    </ExpansionPanelDetails>
                </ExpansionPanel>


                {
                    this.state.recipeIndToShow.map((recipeInd) => {
                        const recipe = this.state.recipes[recipeInd];
                        return (
                            
                            <ExpansionPanel key={recipe._id} >
                                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                                    <Typography className={classes.heading}>{recipe.receptCim}</Typography>
                                </ExpansionPanelSummary>
                                <ExpansionPanelDetails>


      <RecipePage state={recipe} part={1}/>
                                    

                                </ExpansionPanelDetails>
                            </ExpansionPanel>
                        )
                    })
                }

            </div>
        )
    }
}

Search.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Search);
